<template>
  <div>

  
    <!-- 모달창 
    v-if 참일 때 구현 
    1) data에서는 modalOpen: false로 저장
    2) 조건에서는 v-if=" modalOpen == true " -->
    <!-- modalOpen == true 열기
      modalOpen = false 닫기 -->

    <div class="black-bg" v-if=" modalOpen == true ">
      <div class="white-bg">
        <h4>{{ oneRooms[userClick].title }}</h4> <!-- 사용자가 누른 상품 번호 -->
        <p>{{ oneRooms[userClick].content }}</p>
        <button class="close-btn" @click=" modalOpen = false ">닫기</button>
      </div>
    </div>

    <div class="menu">
      <!-- Vue의 HTML반복문
       1) <태그 v-for="작명 iu 몇회" :key="작명"> 
       2) 반복문에 array/object집어넣기 가능 : 자료 안의 데이터 갯수만큼 반복됨 
       3) :key="작명" 용도, 반복문 쓸때 꼭, 반복문 돌린 요소를 컴퓨터가 구문하기 위해 씀
       4) 변수 작명 2개까지 가능 : (a, i) -->
      <a v-for="(a, i) in menuList" :key="i">{{ a }}</a> 
    </div>

    <div v-for="(a,i) in oneRooms" :key="i">
     <img :src="a.image" class="room-img">
      <h4 @click="modalOpen = true; userClick = i ">{{a.title}}</h4>
      <p>{{a.price}}</p>
    </div>
  </div>
</template>

<script>
import data from './assets/oneroom.js';
//import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  data (){ 
    return{ //object 형식으로 저장
      // reacte 에서는 state라고 부름
      userClick : 0,
      oneRooms : data,
      modalClose: false,
      modalOpen: false,
      menuList : ['Home', 'Shop', 'About'],
      price : [60, 70, 80],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸'],
      count: [0, 0, 0]
    }
  },
  methods : {
      increase () {
        this.count += 1
      }
  },
  components: {
  }
}
</script>

